.card{
  width:300px;
  height:350px;
  background:#efefef;
  padding:30px;
  box-sizing:border-box;
  text-align:center;
  border-radius:20px;
  overflow:hidden;
  margin: 10px;
}
.card img{
  width:100px;
  height:100px;
  display:block;
  margin:auto;
  border-radius:50%;
}
.card div h1{
  color: #2980b9;
  text-transform:uppercase;
  margin-top:20px;
  margin-bottom:20px;
  line-height:24px
}
.card div h1 span{
  font-size:14px;
  color:#f39c12
}
.card div ul{
  list-style:none;
}
.card div ul li{
  display:inline-block;
  margin:20px 10px
}
.card div ul li a{
  color:#2980b9;
  font-size:20px;
  transition:0.2s;
}
.card div ul li a:hover{
  color:#000
}
.card div a.btn{
  text-decoration:none;
  color:#000;
  letter-spacing:5px;
  font-weight:bold;
  border:1px solid #000;
  font-weight:bold;
  padding:10px;
  border-radius:2px;
  margin-top:15px;
  display:inline-block;
  transition:0.2s;
}
.card div a.btn:hover{
  border-color:#f39c12
}
.card:before{
  position:absolute;
  content:'';
  width:100px;
  height:100px;
  background:#ddd;
  bottom:0;
  right:0;
  z-index:-1;
  border-top-left-radius:100px;
  transition:0.5s;
}
.card:hover:before{
  transform:scale(10)
}